import { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import axios from "axios";
import Table from "react-bootstrap/Table";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

import { addSeat, setSeats } from "../reducerSlice";

const Component = () => {
  const defNewObj = {
    hall: "",
    number: "",
    row: "",
    price: "",
  };

  const [newSeat, setNewSeat] = useState(defNewObj);
  const apiBase = useSelector((state) => state.toolkit.apiBase);
  const seats = useSelector((state) => state.toolkit.seats);
  const dispatch = useDispatch();

  useEffect(() => {
    axios.get(`${apiBase}/seats`).then((resp) => {
      dispatch(setSeats(resp.data));
    });
  }, [apiBase, dispatch]);

  const addNew = (e) => {
    e.preventDefault();

    axios.post(`${apiBase}/seats`, newSeat).then((resp) => {
      dispatch(addSeat(resp.data));
      setNewSeat(defNewObj);
    });
  };

  const handleChange = (e) => {
    const newMovieTmp = { ...newSeat };

    newMovieTmp[e.target.name] = e.target.value;

    setNewSeat(newMovieTmp);
  };

  return (
    <div className="mb-5 p-2 border border-top-0 rounded-bottom">
      <h3> </h3>

      {seats && (
        <Table striped bordered hover>
          <thead>
            <tr>
              <th>ID</th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {seats.length > 0 &&
              seats.map((x) => {
                return (
                  <tr key={x.id}>
                    <td>{x.id}</td>
                    <td>{x.hall}</td>
                    <td>{x.row}</td>
                    <td>{x.number}</td>
                    <td>{x.price}</td>
                  </tr>
                );
              })}
            {!seats.length && (
              <tr>
                <td>-</td>
                <td>-</td>
                <td>-</td>
                <td>-</td>
                <td>-</td>
              </tr>
            )}
          </tbody>
        </Table>
      )}

      <h3>    </h3>

      <Form onSubmit={addNew}>
        <Row>
          <Col>
            <Form.Group className="mb-3">
              <Form.Label> </Form.Label>
              <Form.Control
                type="number"
                name="hall"
                placeholder=" "
                value={newSeat.hall}
                onChange={handleChange}
              />
              <Form.Text className="text-muted">
                    
              </Form.Text>
            </Form.Group>

            <Form.Group className="mb-3">
              <Form.Label></Form.Label>
              <Form.Control
                type="number"
                name="row"
                placeholder=""
                value={newSeat.row}
                onChange={handleChange}
              />
              <Form.Text className="text-muted">   </Form.Text>
            </Form.Group>
          </Col>

          <Col>
            <Form.Group className="mb-3">
              <Form.Label> </Form.Label>
              <Form.Control
                type="number"
                name="number"
                placeholder=" "
                value={newSeat.number}
                onChange={handleChange}
              />
              <Form.Text className="text-muted">
                    
              </Form.Text>
            </Form.Group>

            <Form.Group className="mb-3">
              <Form.Label></Form.Label>
              <Form.Control
                type="number"
                name="price"
                placeholder=" "
                value={newSeat.price}
                onChange={handleChange}
              />
              <Form.Text className="text-muted">
                    
              </Form.Text>
            </Form.Group>
          </Col>
        </Row>

        <Button variant="primary" type="submit">
          
        </Button>
      </Form>
    </div>
  );
};

export default Component;
